import React from "react";
import { Route, HashRouter, Link } from "react-router-dom";
import Create from "./Components/create";
import Scaffold1 from "./Components/scaffold-1";

import history from "../src/history";
import "./style.css";

class App extends React.Component {
  componentDidMount() {
 
  }
  render() {
    return (
      <HashRouter history={history}>
        <Route path="/" exact>
          <div className="App">
            <div className="navbar" onClick={this.redirect}>
              <div className="title">CV Generator</div>
            </div>

            <div className="main">
              <div className="a"></div>
              <div className="b"></div>
              <div className="c">
                <Link to="/create">
                  <div className="btn" onClick={this.gotoCreate}>
                    Zaczynajmy
                  </div>
                </Link>
              </div>
            </div>

            <footer>Mikołaj Sojka</footer>
          </div>
        </Route>

        

        <Route path="/create">
        <div className="App" id="App">
          <Link to="/">
            <div className="navbar" >
              <div className="title">CV Generator</div>
            </div>
          </Link>
        </div>
          <Create />
          <footer>Mikołaj Sojka</footer>
        </Route>

        <Route path="/scaffold_1">
          <Scaffold1 />
        </Route>
      </HashRouter>
    );
  }
}

export default App;
